@use 'base/variables' as var;
@use 'base/mixins' as mix;

.header{

    background-color: var.$verde;
    @include mix.tablet {
        
    
    &.fijo{
        left: 0;
        top: 0;
        position: fixed;
        width: 100%;
        box-shadow: .1rem .2em .3rem #333;
    }
}
    
h1{
    color: var.$blanco;
    text-align: center;
    @include mix.tablet {
        margin: 0;
    }
}

.contenido-header{
    @media (min-width: 768px) and (max-width: 880px) {
        h1 {
            font-size: 3rem;
        }
    }
}

.contenido-header{

        
    padding: 2rem;
    @include mix.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

}

.navegacion-principal {

    @include mix.tablet {
        display: flex;
        gap: 2rem;
    }


    a{
        display: block;
        text-align: center;
        color: var.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        @include mix.tablet {
            margin: 0;
        }

        &:hover{
            color: var.$amarillo;
        }

    }
}
