@use 'base/variables' as var;
@use 'base/mixins' as mix;

.video{
    position: relative;
    max-height: 70rem;
    min-height: 70rem;
    overflow: hidden;

    .overlay{
        position: absolute;
       background: rgb(126,2,195);
       background: linear-gradient(270deg, rgba(126,2,195,0.5) 0%, rgba(255,0,110,0.3) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    video{
        width: auto;
        display: block;
        @include mix.desktop{
            width: 100%;
        }

        @include mix.telefono{
           width: 100%;
       
        }

        @include mix.tablet {
            width: 100%;
        }
    }
}

.contenido-video{
    z-index: 10;
    h2{
        color: var.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    
    p{
        background-color: var.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: var.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;

    }
}
