@use 'base/mixins' as mix;
@use 'base/variables' as var;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    @include mix.resetear-lista;
    @include mix.grid(2, 2rem);

    @include mix.telefono {
        @include mix.grid(3,2rem);
    }

    @include mix.tablet {
        @include mix.grid(4, 2rem);
    }

    @include mix.desktop {
        @include mix.grid(6, 2rem);
    }
    &:hover{
        cursor: pointer;
    }
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 /.5);

    picture{
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: var.$blanco;
    color: var.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover{
        cursor: pointer;
    }
}